import { Inject, Injectable, Optional } from '@angular/core';

import {
  INITIATE_PAYMENT_JOURNEY_COMMUNICATOR,
  InitiatePaymentJourneyCommunicationService,
} from './communication.service';

/** @internal */
@Injectable()
export class InitiatePaymentRoutableModalService {
  constructor(
    @Optional()
    @Inject(INITIATE_PAYMENT_JOURNEY_COMMUNICATOR)
    private readonly communicator: InitiatePaymentJourneyCommunicationService
  ) {}

  closeModal(): void {
    this.communicator?.closeEvent();
  }

  openModal(): void {
    this.communicator?.headerNavigationAction('new-debit-ach');
  }
}
